// Important Components
  import React from "react";
  import { BrowserRouter as Router, Routes, Route, Link, NavLink } from "react-router-dom";
// Pages
  import Main from '../pages/main/main';
  import AboutMe from '../pages/aboutme/aboutme';
  import SmmSpace from '../pages/smmspace/smmspace';
  import Education from '../pages/education/education';
  import Contact from '../pages/contact/contact';
  import TermsOfUse from "../pages/termsofuse/termsofuse";
  import BasicCourse from "../pages/crs/basic/crs-basic";
  import BasicCB from "../pages/education/basic/cbasic";
// Stylesheet
  import styles from './header.module.scss';
// Hamburger
  import HamburgerMenu from '../components/hamburger/h/hamburger';
// Translation Service
  import { useTranslation } from "react-i18next";
  import ModalHeadLang from "../components/modal/modalHeadLang/modalHeadLang";


const Header = () => {
  const { t, i18n } = useTranslation ();
  return (
    <>
      <Router>
        <header>
          <div className={styles.headerWrapper}>
            <a className={styles.headerLogo} href='/'>Lina Punder</a>

            <div className={styles.headerNavWrapper}>
              <ul className={styles.headerNav}>
                <li>  <NavLink exact className={styles.headerNavLink} activeClassName={styles.headerNavLink__Active}  to='AboutMe'>        {t('PageTitle.aboutme')}         </NavLink>      </li>
                <li>  <NavLink exact className={styles.headerNavLink} activeClassName={styles.headerNavLink__Active}  to='SmmSpace'>       {t('PageTitle.smmspace')}        </NavLink>      </li>
                <li>  <NavLink exact className={styles.headerNavLink} activeClassName={styles.headerNavLink__Active}  to='Education'>      {t('PageTitle.education')}       </NavLink>      </li>
                <li>  <NavLink exact className={styles.headerNavLink} activeClassName={styles.headerNavLink__Active}  to='Contact'>        {t('PageTitle.contact')}         </NavLink>      </li>
                {/* <li><ModalHeadLang /></li> */}
                <li><HamburgerMenu /></li>
              </ul>
            </div>
          </div>

          <Routes>
            <Route path='/' element={<Main />} />
            <Route path='AboutMe' element={<AboutMe />} />
            <Route path='SmmSpace' element={<SmmSpace />} />
            <Route path='Education' element={<Education />} />
            <Route path='Contact' element={<Contact />} />
            <Route path='TermsOfUse' element={<TermsOfUse />} />
            <Route path='BasicCourse' element={<BasicCourse />} />
            <Route path='/education/BasicCB,betaversion' element={<BasicCB />} />
          </Routes>
        </header>
      </Router>
    </>
  );
};

export default Header;