// React Configurations
  import React from "react";
// Style's Configuration
  import styles from './contact.module.scss';
// Translation Service
  import { useTranslation } from "react-i18next";
// Forms
  import ContactUs from "../../components/ContactForm/ContactForm";

const Contacts = () => {
  const { t, i18n } = useTranslation ();
  return (
    <>
      <div className={styles.wrapper}>
        <div className={styles.unit}>

          <div className={styles.unit__b1}>
            <section className={styles.ContactSection1}>
              <span className={styles.ContactSection1__u1}>{t('ContactPage_1.s1t1')}</span><br/>
              <span>{t('ContactPage_1.s1t2')}</span><br/>             
              <div className={styles.buttonsUnit}>
              <form action="https://t.me/lina_smm">
                  <button
                  className={styles['btn-tg']}
                  type="submit">
                  <i class="fab fa-telegram"/>
                  <span className={styles['btn-label']}>Telegram</span>
                  <span className={styles['btn-brief']}>@lina_smm</span>
                  </button>
              </form>
              <form action="mailto:lina_punder@ukr.net">
                  <button
                  className={styles['btn-email']}
                  type="submit">
                  <i class="fas fa-envelope" />
                  <span className={styles['btn-label']}>Email</span>
                  <span className={styles['btn-brief']}>lina_punder@ukr.net</span>
                  </button>
              </form>
              </div>
            </section><br/>
            <section className={styles.ContactSection2}>
              <span className={styles.ContactSection2__u1}>{t('ContactPage_1.s2t1')}</span><br/>  
              <div className={styles.buttonsUnit}>
              <form action="https://www.instagram.com/wonder_woman_smm/">
                  <button
                  className={styles['btn-ig']}
                  type="submit">
                  <i class="fab fa-instagram" />
                  <span className={styles['btn-label']}>Instagram</span>
                  <span className={styles['btn-brief']}>@wonder_woman_smm</span>
                  </button>
              </form>
              <form action="https://www.facebook.com/people/Lina-Punder/100013393308012/#/">
                  <button
                  className={styles['btn-fb']}
                  type="submit">
                  <i class="fab fa-facebook" />
                  <span className={styles['btn-label']}>Facebook</span>
                  <span className={styles['btn-brief']}>/Lina-Punder</span>
                  </button>
              </form>
              </div>
            </section>
          </div>

          <div className={styles.unit__b2}>
            <section>
              <ContactUs />
            </section>
          </div>

        </div>
      </div>
    </>
  );
};

export default Contacts;