// управління обгорткою посилань
const Navigation = styled.nav`
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 600;
  width: ${(props) => (props.clicked ? "100%" : "0")};
  opacity: ${(props) => (props.clicked ? "1" : "0")};

  transition: width 0.8s, opacity 0.8s;
`;
//управління списком
const List = styled.ul`
  position: absolute;
  list-style: none;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
  text-align: left;
  width: 100%;
`;

//управління посиалннями
const ItemLink = styled(NavLink)`
  display: inline-block;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2rem;
  font-weight: 300;
  text-decoration: none;
  color: ${COLORS.NavLink};
  padding: 1rem 2rem;

  background-image: linear-gradient(
    110deg,
    transparent 0%,
    transparent 50%,
    ${COLORS.ItemLinkHaver} 50%
  );
  background-size: 940%;
  transition: all 0.4s;

  &:hover,
  &:active {
    background-position: 100%;
    color: ${COLORS.NavLink};
    font-weight: 600;
    transform: translateX(1rem);
  }
`;

function HamburgerMenu() {
  const [click, setClick] = useState(false);
  const { t, i18n } = useTranslation ();
  const handleClick = () => setClick(!click);
  return (
    <>
      <MenuLabel htmlFor="navi-toggle" onClick={handleClick}>
        <button clicked={click} className={styles.btnOpen}><i class="fas fa-bars"/></button>
        {/* <Icon clicked={click}>&nbsp;</Icon> */}
      </MenuLabel>
      <NavBackground clicked={click}>&nbsp;</NavBackground>
      <Navigation clicked={click}>
        <List>
          <li>
            <ItemLink onClick={handleClick} to="/AboutMe">
            {t('PageTitle.aboutme')}
            </ItemLink>
          </li>
          <li>
            <ItemLink onClick={handleClick} to="/SmmSpace">
            {t('PageTitle.smmspace')}
            </ItemLink>
          </li>
          <li>
            <ItemLink onClick={handleClick} to="/Education">
                {t('PageTitle.education')}
            </ItemLink>
          </li>
          <li>
            <ItemLink onClick={handleClick} to="/Contact">
            {t('PageTitle.contact')}
            </ItemLink>
          </li>
        </List>
      </Navigation>
    </>
  );
}

export default HamburgerMenu; 