import React from 'react';
import Modal from 'react-modal';
import styles from './modalHeadLang.module.scss';
import LanguageSelector from '../../LanguageSelector/LanguageSelector';

const customStyles = {
  content: {
    // position: 'abolute',
    top: '100px',
    left: '100px',
    right: '100px',
    bottom: '100px',
    border: '1px solid #ccc',
    background: '#fff',
    overflow: 'auto',
    WebkitOverflowScrolling: 'touch',
    borderRadius: '4px',
    outline: 'none',
    padding: '20px',
  },
};

function ModalHeadLang() {
  let subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#6D28D9';
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div>
      <button onClick={openModal}><i class="fas fa-language"/></button>
      <Modal 
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        {/* <h2 ref={(_subtitle) => (subtitle = _subtitle)}>CHOOSE YOUR LANGUAGE</h2> */}
        <button onClick={closeModal}>close</button>
        <div>I am a modal</div>
        <LanguageSelector/>

      </Modal>
    </div>
  );
}

export default ModalHeadLang;