import emailjs from '@emailjs/browser';
import React from 'react';
import Modal from 'react-modal';
import styles from './ModalEnrollment.module.scss';
// import LanguageSelector from '../../LanguageSelector/LanguageSelector';
import Swal from 'sweetalert2';

// const SERVICE_ID = "1";
// const TEMPLATE_ID = "2";
// const USER_ID = "3";

const SERVICE_ID = "service_j5s35sj";
const TEMPLATE_ID = "template_hfuat1m";
const USER_ID = "user_83Y09Pqx0wTTFbnAX6M8D";

const customStyles = {
  content: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    top: '100px',
    // left: '50px',
    // right: '50px',
    bottom: '100px',
    border: '1px solid #ccc',
    background: '#fff',
    overflow: 'auto',
    WebkitOverflowScrolling: 'touch',
    borderRadius: '10px',
    outline: 'none',
    padding: '20px',
  },
};

function ModalEnrollment() {
  let subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#6D28D9';
  }

  function closeModal() {
    setIsOpen(false);
  }

  function sendEmail(e) {
    e.preventDefault();
    emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID)
      .then((result) => {
        console.log(result.text);
        Swal.fire({
          icon: 'success',
          title: ''
        })
      }, (error) => {
        console.log(error.text);
        Swal.fire({
          icon: 'error',
          title: 'Ууупс, щось пішло не так',
          text: error.text,
        })
      });
    e.target.reset()
  };

  return (
    <div>
      <button onClick={openModal}><i class="fas fa-book-reader" /><br/>Записатись на навчання просто зараз</button>
      <Modal 
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        {/* <h2 ref={(_subtitle) => (subtitle = _subtitle)}>CHOOSE YOUR LANGUAGE</h2> */}
        <div className={styles.form}>
          <form onSubmit={sendEmail}>
            <div>

            <input type="hidden" name="srvsPage" value="Контакти"/>
            <input type="hidden" name="srvsCrs" value="Базовий"/>

            <div className={styles.formgroup}>
              <input type="text" className={styles.input} placeholder="" name="name" required/>
              <span className={styles.highlight}></span>
              <span className={styles.label}>Твоє Ім'я та Прізвище</span>
            </div>

            <div className={styles.formgroup}>
              <input type="email" className={styles.input} placeholder="" name="email" required/>
              <span className={styles.highlight}></span>
              <span className={styles.label}>Твоя електронна адреса</span>
              <span className={styles.error} id='email-error'></span>
            </div>

            <div className={styles.formgroup}>
              <input type="text" className={styles.input} placeholder="" name="tel" required/>
              <span className={styles.highlight}></span>
              <span className={styles.label}>Твій контактний телефон</span>
              <span className={styles.error} id='email-error'></span>
            </div>

            {/* <div className={styles.formgroup}>
              <textarea className={styles.textarea}
              tabindex='4' cols="20" rows="5" placeholder="" name="message"></textarea>
              <span className={styles.highlight}></span>
              <label className={styles.label}>Тут напиши своє питання</label> 
            </div> */}

            <div className={styles.buttonsSection}>
              <input type="submit" className={styles.button} value="Send Message"></input>
              <br/>
              <input type="reset" className={styles.buttonReset} value="Очистити форму"></input>
              <button onClick={closeModal} className={styles.BtnClose}><i class="fas fa-times-circle"/>Закрити форму</button>
            </div>

            </div>
          </form>
        </div>
      </Modal>
    </div>
  );
}

export default ModalEnrollment;