// Configuration
import React from 'react';
// import Modal from 'react-modal';
import i18next from '../../i18next';
// Design
import styles from './LanguageSelector.module.scss';


function LanguageSelector() {
  function handleClick(lang) {
    i18next.changeLanguage(lang)
  }
  
  const [setIsOpen] = React.useState(false);
  
  function closeModal() {
    setIsOpen(false);
  }

  return (
  
    <div className={styles.LanguageSelectorSection}>
      <div className={styles.select}>
        <button onClick={()=>handleClick('uk')}>Українська</button><br/>
        <button onClick={()=>handleClick('en')}>English</button><br/>
        <button onClick={()=>handleClick('ru')}>Русский</button><br/>

      </div>
    </div>
  
  );
}

export default LanguageSelector;