// React Configurations
  import React from "react";
// Style's Configuration
  import styles from './education.module.scss';
// Translation Service
  import { useTranslation } from "react-i18next";
// Content Connection
  import im1 from '../../assets/content/pgs/edu1.png';
  import im2 from '../../assets/content/pgs/edu2.png';
  // import im3 from '../../assets/content/pgs/edu3.png';

  // <img src={img1} className={styles.CourseBannerBriefImage} alt='None' />


// <i class="far fa-hand-point-right"></i>

